<template>
    <form @submit.prevent="onSubmit"> 
    <!--@:用于事件处理的特殊指令：v-on-->
        <input
            type="text"
            id="new-todo-input"
            name="new-todo"
            autocomplete="off"
            v-model.lazy.trim="label"
            class="input__lg"
        />
        <button type="submit" class="btn btn__primary">Add</button>
    </form>
</template>

<script>
    export default {
        methods: {
            onSubmit() {
                if(this.label ===""){
                    return;
                }
                this.$emit("todo-added", this.label); // $emit是Vue实例的一个方法，用于在组件内部触发一个自定义事件。在这个例子中，它触发了一个名为todo-added的事件。
                this.label = "";
            }
        }
    };
</script>

<style scoped>
    .h2-label-lg {
        font-size: 1.3rem;
        font-weight: 700;
        display: block;
        color: #fff;
    }
</style>