<template>
    <div class="stack-small" v-if="!isEditing">
    <div class="custom-checkbox">
        <input type="checkbox" class="checkbox" 
        :id="id" 
        :checked="isDone"
        @change="$emit('checkbox-changed')"  circle />
        <label :for="id" class="checkbox-label">{{ label }}</label>  
        <!--{{}}是 Vue 中的一个特殊的模版语法，它能在模版内打印类中定义的 JavaScript 表达式的结果，包括值和方法-->
    </div>
    <div class="btn-group">
        <button 
            type="primary"
            class="btn"
            ref="editButton"
            @click="toggleToItemEditForm">
            Edit <span class="visually-hidden">{{ label }}</span>
        </button>
        <button type="button" class="btn btn__danger" @click="deleteToDo">
            Delete <span class="visually-hidden">{{ label }}</span>
        </button>
    </div>
    </div>
    <to-do-item-edit-form
    v-else
    :id="id"
    :label="label"
    @item-edited="itemEdited"
    @edit-cancelled="editCancaeled">
    </to-do-item-edit-form>
</template>
<script>
import ToDoItemEditForm from './ToDoItemEditForm.vue';

export default{
    components:{
        ToDoItemEditForm,
    },
    props: {
        label:{required: true, type: String},
        done:{default: false, type: Boolean},
        id:{required: true, type: String},
    },
    data(){
        return{
            isEditing: false,
        };
    },
    computed:{
        isDone(){
            return this.done;
        }
    },
    methods:{
        deleteToDo(){
            this.$emit('item-deleted');
        },
        toggleToItemEditForm(){
            console.log(this.$refs.editButton);
            this.isEditing = true;
        },
        itemEdited(newLabel){
            this.$emit('item-edited', newLabel);
            this.isEditing = false;
            this.focusOnEditButton();
        },
        editCancaeled(){
            this.isEditing = false;
            this.focusOnEditButton();
        },
        focusOnEditButton(){
            this.$nextTick(() => {
                const editButtonRef = this.$refs.editButton;
                editButtonRef.focus();
            });
        },
    },
};
</script>

<style scoped>
    /* scoped为你所有的样式附加了一个独特的 HTML data 属性选择器，防止它们在全局范围内发生冲突 */
.custom-checkbox > .checkbox-label {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.25;
  color: #0b0c0c;
  display: block;
  margin-bottom: 5px;
}
.custom-checkbox > .checkbox {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.25;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  margin-top: 0;
  padding: 0;
  border-radius: 50%;
  appearance: none;
  border: 2px solid #000;
}
.custom-checkbox > input:focus {
  outline: 3px dashed #fd0;
  outline-offset: 0;
  box-shadow: inset 0 0 0 2px;
}
.custom-checkbox {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
  display: block;
  position: relative;
  min-height: 40px;
  margin-bottom: 10px;
  padding-left: 40px;
  clear: left;
}
.custom-checkbox > input[type="checkbox"] {
  -webkit-font-smoothing: antialiased;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: -2px;
  left: -2px;
  width: 44px;
  height: 44px;
  margin: 0;
  opacity: 0;
  
}
.custom-checkbox > .checkbox-label {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  display: inline-block;
  margin-bottom: 0;
  padding: 8px 15px 5px;
  cursor: pointer;
  touch-action: manipulation;
}
.custom-checkbox > label::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border: 2px solid currentcolor;
  background: transparent;
}
.custom-checkbox > input[type="checkbox"]:focus + label::before {
  border-width: 4px;
  outline: 3px dashed #6fabe4;
}
.custom-checkbox > label::after {
  box-sizing: content-box;
  content: "";
  position: absolute;
  top: 11px;
  left: 9px;
  width: 18px;
  height: 7px;
  transform: rotate(-45deg);
  border: solid;
  border-width: 0 0 5px 5px;
  border-top-color: transparent;
  opacity: 0;
  background: transparent;
}
.custom-checkbox > input[type="checkbox"]:checked + label::after {
  opacity: 1;
}
@media only screen and (min-width: 40rem) {
  label,
  input,
  .custom-checkbox {
    font-size: 19px;
    font-size: 1.9rem;
    line-height: 1.31579;
  }
}

</style>